.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 176px;
  height: 46px;
  line-height: 46px;
  font-weight: 600;
  font-size: 13px;
  color: #FFFFFF;
  background: var(--main-color);
  cursor: pointer;
  transition: all 0.4s;
  &--form,
  &--reviews {
    border: 1px solid transparent;
    width: 258px;
  }
  &:hover {
    background: none;
    border: 1px solid var(--main-color);
    color: var(--main-color);
    svg {
      fill: var(--main-color);
    }
  }
}
