.form {
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  &__group {
    width: 273px;
    position: relative;
    margin-right: 14px;
  }
  &__input {
    width: 100%;
    padding: 13px 15px;
    display: block;
    width: 100%;
    color: #000000;
    background: #FFFFFF;
    border: 1px solid #D5D5D5;
    font-weight: 400;
    font-size: 14px;
  }
  &__input-check {
    margin-right: 16px;
  }
  &__input::-webkit-input-placeholder {
    color: #565656;
  }
  &__input::-moz-placeholder {
    color: #565656;
  }
  &__input:-moz-placeholder {
    color: #565656;
  }
  &__input:-ms-input-placeholder {
    color: #565656;
  }
  &__label {
    position: absolute;
    top: -23px;
    font-weight: 500;
    font-size: 12px;
    color: #000000;
  }
  &__label-check {
    font-weight: 400;
    font-size: 14px;
    color: #000000;
  }
}


/* Чекбокс */
.custom-checkbox {
  position: absolute;
  width: 24px;
  height: 24px;
  opacity: 0;
}

.custom-checkbox + label {
  display: inline-flex;
  align-items: center;
  user-select: none;
}

.custom-checkbox + label::before {
  content: "";
  display: inline-block;
  width: 24px;
  height: 24px;
  margin-right: 16px;
  background: #F9F9F9;
  border: 1px solid #D5D5D5;
  background-repeat: no-repeat;
  background-position: center center;
}

.custom-checkbox:checked + label::before {
  background-image: url("../../img/svg/check.svg");
}
