.contact {
  padding: 75px 0;
  background-color: #043C77;
  margin: 0 auto;
  &__title {
    color: #fff;
    text-align: center;
  }
  &__descr {
    max-width: 565px;
    margin: 30px auto 0 auto;
    text-align: center;
    font-size: 15px;
    font-weight: 400;
    color: #fff;
  }
  &__form {
    margin-top: 50px;
    justify-content: center;
  }
  &__info {
    margin-top: 20px;
    text-align: center;
    font-size: 12px;
    color: #fff;
    &-link {
      color: #fff;
      text-decoration: underline;
    }
  }
  &__label {
    color: #fff;
  }
  &__btn {
    &:hover {
      border-color: #fff;
      color: #fff;
    }
  }
}
