.custom-select {
  position: relative;
  user-select: none;
  &__dropdown {
    display: none;
    position: absolute;
    left: 0;
    top: calc(100% - 1px);
    width: 100%;
    min-width: 115px;
    z-index: 20;
    background-color: #FFFFFF;
    border: 1px solid #D5D5D5;
    border-top: none;
  }

  &__item {
    cursor: pointer;
    padding: 15px;
    width: 100%;
    transition: all 0.4s;
    &:not(:first-child) {
      border-top: 1px solid #D5D5D5;
    }
    &:hover {
      color: #fff;
      background-color: var(--main-color);
      transition: color 0.3s;
    }
  }

  &__top {
    position: relative;
    padding: 15px;
    text-transform: uppercase;
    background: #FFFFFF;
    border: 1px solid #D5D5D5;
    padding-right: 34px;
    cursor: pointer;
    &::after {
      content: "";
      position: absolute;
      right: 7px;
      top: 50%;
      transform: translateY(-50%);
      width: 24px;
      height: 24px;
      background-image: url('../../img/svg/arrow-down.svg');
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      transition: transform 0.4s;
    }
  }

  &--open &__top:after {
    transform: translateY(-50%) rotate(180deg);
  }
  &--open &__dropdown {
    display: block;
  }
}








