.advantages {
  background: #F9F9F9;
  padding: 80px 0 72px 0;
  &__inner {
    margin-top: 48px;
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    justify-content: space-between;
  }

  &__item {
    padding: 20px;
    display: flex;
    align-items: flex-start;
    width: 32%;
    background: #fff;
    &-content {
      margin-left: 15px;
      max-width: 250px;
    }
    &-icon {
      display: block;
      width: 46px;
      height: 46px;
    }
    &-descr {
      margin-top: 12px;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;
      color: #565656;
    }
    &:not(:nth-last-child(-n+3)) {
      margin-bottom: 30px;
    }
  }
}
