.footer {
  background-color: #F9F9F9;
  figcaption {
    margin-top: 10px;
    font-weight: 400;
    font-size: 13px;
    color: #000000;
  }
  &__top {
    padding: 24px 0;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }
  &__bottom {
    padding: 15px 0;
    background-color: #fff;
    &-policy {
      font-weight: 400;
      font-size: 14px;
      color: #000;
      transition: color 0.4s;
      &:hover {
        color: var(--main-color);
      }
    }
  }

  &__item {
    margin-bottom: 5px;
  }

  &__link {
    font-weight: 600;
    font-size: 15px;
    line-height: 18px;
    color: #000000;
    transition: color 0.4s;
    &:hover {
      color: var(--main-color);
    }
  }

  &__contact {
    font-weight: 600;
    font-size: 16px;
    color: #000000;
    text-align: right;
    &-phone {
      margin-top: 5px;
      padding-bottom: 3px;
      display: block;
      font-size: 18px;
      color: var(--main-color);
      transition: border 0.2s;
      border-bottom: 2px solid transparent;
      &:hover {
        border-bottom: 2px solid var(--main-color);
      }
    }
  }
}
