.reviews {
  padding: 86px 0 80px 0;
  background-color: #F9F9F9;
  &__slider {
    position: relative;
  }
  &__item {
    padding: 40px;
    width: 545px;
    margin: 0 15px;
    background-color: #fff;
    &-descr {
      margin-top: 12px;
      font-weight: 500;
      font-size: 12px;
      line-height: 15px;
      color: #565656;
    }
    &-text {
      margin-top: 40px;
      font-weight: 400;
      font-size: 16px;
      line-height: 140%;
      color: #000000;
    }
    &-link {
      margin-top: 40px;
      display: block;
      font-weight: 600;
      font-size: 13px;
      line-height: 16px;
      color: var(--main-color);
      &:hover {
        text-decoration: underline;
      }
    }
  }
  &__more {
    display: block;
    text-align: center;
    margin: 44px auto 0 auto;
    background-color: #043C77;
    &:hover {
      border-color:#043C77;
      color:#043C77;
    }
  }

  .slick-track {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .slick-list {
    display: flex;
    align-items: stretch;
    padding: 50px 0 50px 0;
    overflow: hidden;
  }

  & .slick-prev,
  & .slick-next {
    position: absolute;
    top: -22px;
    right: 0;
    width: 56px;
    height: 56px;
    border: 1px solid #D5D5D5;
    background: none;
    display: flex;
    align-items: center;
    justify-content: center;
    &:before {
      display: none;
    }
  }

  & .slick-prev {
    left: calc(100% - 125px);
  }
}
