@include for-desktop {
  .delivery {
    &__order {
      padding: 53px 40px 40px 40px;
    }
  }
  .price {
    display: none;
  }

  .order {
    &-info {
      padding: 30px;
    }
  }

  .steps {
    &__item {
      width: 300px;
    }
  }

  .advantages {
    &__item {
      width: 48%;
      &:not(:nth-last-child(-n+3)) {
        margin-bottom: 0px;
      }
      &:not(:nth-last-child(-n+2)) {
        margin-bottom: 30px;
      }
    }
  }
}

@include tablet {
  .header {
    &__phone {
      font-size: 0;
      svg {
        padding: 5px;
        width: 30px;
        height: 30px;
        border: 2px solid #000;
        border-radius: 100%;
        transition: border 0.4s;
        &:hover {
          border-color: var(--main-color);
        }
      }
    }
    &__bottom {
      padding-top: 90px;
      background: var(--main-color);
      position: absolute;
      top: 0;
      left: -100%;
      height: 100vh;
      width: 70%;
      transition: left 0.6s;
      z-index: 1;
      &-close {
        display: block;
        position: absolute;
        top: 20px;
        right: 20px;
        font-size: 35px;
        color: #fff;
        transition: color 0.4s;
        cursor:pointer;
        &:hover {
          color: red;
        }
      }
      &--active {
        left: 0;
      }
    }

    &__nav {
      &-list {
        flex-direction: column;
      }
      &-item {
        margin-right: 0;
        &:not(:last-child) {
          margin-bottom: 20px;
        }
      }
      &-link {
        color: #fff;
        svg {
          fill: #fff;
        }
        &:hover {
          color: #fff;
          text-decoration: underline;
          svg {
            fill: #fff;
          }
        }
      }
    }

    .burger {
      @include burger;
      margin-left: 20px;
    }

  }

  .delivery {
    &__order {
      &-title {
        text-align: center;
      }
    }
    &__form {
      margin-top: 25px;
      flex-direction: column;
      align-items: center;
      &-group {
        margin-top: 40px;
        margin-right: 0;
      }
      &-checkbox {
        text-align: center;
      }
    }
    &__btn {
      margin-top: 20px;
    }
  }

  .order {
    &__title {
      text-align: center;
    }
    &__inner {
      flex-wrap: wrap;
      justify-content: center;
    }
    &-info {
      margin-top: 30px;
    }
  }

  .steps {
    &__title {
      text-align: center;
    }
    &__item {
      width: 32%;
      min-height: 415px;
      max-height: 415px;
      &:not(:last-child) {
        margin-bottom: 20px;
      }
      &-title {
        font-size: 18px;
      }
      &-descr {
        font-size: 14px;
        &--mini {
          font-size: 10px;
        }
      }
    }
  }

  .contact {
    &__form {
      margin-top: 20px;
      flex-direction: column;
      &-group {
        margin-top: 40px;
        margin-right: 0;
      }
    }

    &__btn {
      width: 273px;
      margin-top: 20px;
    }
  }
}


@include small-tablet {
  .header {
    &__contact-btn,
    &__user-btn {
      width: 70px;
      height: 40px;
      font-size: 0;
      svg {
        display: block;
        margin-right: 0;
      }
    }

    &__phone {
      margin-right: 15px;
      font-size: 0;
    }
  }

  .delivery {
    &__title {
      font-size: 40px;
    }
    &__order {
      &-title {
        font-size: 30px;
      }
    }
  }

  .order {
    &-info {
      &__total-text,
      &__part-text,
      &__benefit-text {
        p {
          max-width: 220px;
        }
      }
    }
  }

  .steps {
    &__inner {
        flex-direction: column;
        align-items: center;
    }
    &__item {
      width: 353px;
    }
  }

  .advantages {
    &__item {
      &-content {
        max-width: 180px;
      }
      &-title {
        font-size: 13px;
      }
      &-descr {
        font-size: 12px;
      }
    }
  }

  .certificate {
    .slick-prev,
    .slick-next {
      top: auto;
      bottom: -45px;
      z-index: 1;
    }

    .slick-prev {
      left: 200px;
    }
    .slick-next {
      right: 200px;
    }
  }

  .partner {
    padding: 100px 0;
  }

  .footer {
    &__top {
      flex-direction: column;
      align-items: center;
    }

    &__list {
      margin-top: 20px;
    }

    &__contact {
      text-align: center;
      margin-top: 20px;
    }

    &__bottom {
      text-align: center;
    }
  }
}


@include mobile {
  .header {
    &__top {
      flex-direction: column;
    }
    &__content {
      padding-left: 35px;
      width: 100%;
      justify-content: space-between;
    }
    &__phone {
      svg {
        margin-right: 5px;
      }
    }
  }

  .delivery {
    &__title {
      font-size: 29px;
    }
    &__order {
      padding:53px 20px 40px 20px;
      &-title {
        font-size: 22px;
      }
    }
    &__form {
      &-group {
        width: 252px;
      }
    }
    &__btn {
      width: 252px;
    }
  }

  .order {
    &__title {
      font-size: 30px;
    }
    &-info {
      padding: 30px 20px;
    }
    &__form {
      &-group {
        width: 300px;
      }
    }
  }

  .steps {
    &__title {
      font-size: 30px;
    }
    &__item {
      width: 100%;
    }
  }

  .advantages {
    &__title {
      font-size: 30px;
    }
    &__item {
      flex-direction: column;
      align-items: center;
      width: 100%;
      &-content {
        text-align: center;
        margin-left: 0;
        margin-top: 20px;
        max-width: 100%;
      }
      &:not(:nth-last-child(-n+2)) {
        margin-bottom: 0px;
      }
      &:not(:last-child) {
        margin-bottom: 30px;
      }
    }
  }

  .certificate {
    &__title {
      font-size: 30px;
    }
    &__item {
      margin: 0 auto;
    }

    .slick-prev,
    .slick-next {
      top: auto;
      bottom: -45px;
      z-index: 1;
    }

    .slick-prev {
      left: 30%;
    }
    .slick-next {
      right: 30%;
    }
  }

  .reviews {
    &__item {
      padding: 25px;
    }
  }
}
