.header {
  background-color: #fff;
  &__top {
    padding: 9px 0 15px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__btns, &__content {
    display: flex;
    align-items: center;
  }

  &__content {
    margin-top: 8px;
  }

  &__phone {
    margin-right: 35px;
    position: relative;
    font-weight: 600;
    font-size: 18px;
    color: #000000;
    transition: all 0.4s;
    &-icon {
      position: absolute;
      top: 50%;
      left: -31px;
      transform: translateY(-50%);
      width: 20px;
      height: 20px;
      fill:#000
    }
    &:hover {
      color: var(--main-color);
      svg {
        fill: var(--main-color);
      }
    }
  }

  &__user-btn, &__contact-btn {
    svg {
      margin-right: 10px;
      width: 20px;
      height: 20px;
      fill: #fff;
    }
  }

  &__contact-btn {
    svg {
      display: none;
    }
  }

  &__bottom {
    padding: 21px 0;
    background-color: #F9F9F9;
    &-close {
      display: none;
    }
  }
  &__nav {
    &-list {
      display: flex;
      align-items: center;
    }
    &-item {
      margin-right: 45px;
    }
    &-link {
      position: relative;
      font-weight: 600;
      font-size: 15px;
      color: #000000;
      transition: all 0.4s;
      &:hover {
        color: var(--main-color);
        svg {
          fill: var(--main-color);
        }
      }
    }
    &-icon {
      position: absolute;
      top: 50%;
      right: -10px;
      transform: translateY(-50%);
      width: 10px;
      height: 10px;
    }
  }

  .burger {
    display: none;
  }
}
