.price {
  padding: 100px 0;

  &__inner {
    margin-top: 50px;
  }

  &__head {
    display: grid;
    align-items: center;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 56px;
    color: #fff;
    background-color: #043C77;

    &-title,
    &-titles {
      font-weight: 600;
      font-size: 13px;
      color: #FFFFFF;
      justify-self: center;
    }

    &-titles {
      padding-left: 30px;
      padding-right: 15px;
      display: grid;
      grid-template-columns: minmax(280px, auto) minmax(177px, auto) minmax(150px, auto);
      column-gap: 10px;
    }
  }

  &__body {
    margin-top: 20px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    border: 1px solid #E7E7E7;
    border-left: none;
    &-name {
      padding: 16px 20px;
      grid-row: 1/9;
      background: #098FCF;
      font-weight: 700;
      font-size: 24px;
      color: #FFFFFF;
    }

    &-class {
      padding: 16px 30px;
      font-weight: 600;
      font-size: 13px;
      color: #000000;
      border-right: 1px solid #E7E7E7;

      &--dt5 {
        grid-row: 1/4;
      }

      &--dt4 {
        grid-row: 4/6;
      }

      &--ai5 {
        grid-row: 1/4;
      }

      &--ai4 {
        grid-row: 4/6;
      }

      &--ai3 {
        padding: 64px 30px;
        grid-row: 6/9;
      }

      &:not(:first-child) {
        border-top: 1px solid #E7E7E7;
      }
    }

    &-info {
      padding: 15px 30px;
      display: grid;
      grid-template-columns: minmax(280px, auto) minmax(177px, auto) minmax(150px, auto);
      column-gap: 10px;

      div {
        font-weight: 600;
        font-size: 13px;
        color: #000000;
      }

      &:not(:last-child) {
        border-bottom: 1px solid #E7E7E7;
      }
    }

    &--dt {
      grid-template-rows: repeat(5, 46px);
    }

    &--ai {
      grid-template-rows: repeat(8, 46px);
    }
  }
}
