.delivery {
  padding-top: 90px;
  &__form {
    margin-top: 55px;
    &-checkbox {
      width: 100%;
      margin-top: 30px;
    }
  }
  &__order {
    padding: 53px 96px 40px 96px;
    margin-top: 48px;
    background: #FFFFFF;
    box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.1);
    &-title span {
      color: var(--main-color);
    }
  }

  &__label {
    &-check {
      font-weight: 400;
      font-size: 14px;
      color: #000000;
    }
  }
}
