.order {
  padding: 74px 0 73px 0;
  background: #F9F9F9;
  &__inner {
    margin-top: 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__form {
    flex-direction: column;
    align-items: flex-start;
    &-group {
      width: 409px;
      &:not(:first-child) {
        margin: 39px 0 0 0;
      }
    }
  }

  &-info {
    padding: 40px;
    width: 638px;
    background: #FFFFFF;
    box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.1);
    &__title {
      padding-bottom: 15px;
      font-weight: 500;
      color: #000000;
    }
    &__total,
    &__part,
    &__benefit {
      padding: 16px 0;
      border-top: 1px solid #E7E7E7;
      &-content {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
      }
      &-text {
        font-weight: 400;
        font-size: 16px;
        line-height: 140%;
        color: #000000;
      }
      &-descr {
        margin-top: 10px;
        max-width: 350px;
        font-weight: 400;
        font-size: 12px;
        line-height: 15px;
        color: #565656;
      }

      &-price {
        font-weight: 400;
        font-size: 16px;
        line-height: 140%;
        text-align: right;
        color: #000000;
        &--bold {
          font-weight: 600;
          font-size: 18px;
        }
      }
    }
  }

  &__btn {
    margin-top: 15px;
  }
}
