.certificate {
  padding-top: 100px;
  &__slider {
    position: relative;
  }
  &__item {
    width: 257px !important;
    padding: 28px 20px;
    margin: 0 15px;
    background-color: #fff;
    box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.1);
  }

  .slick-track {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .slick-list {
    padding: 52px 0 50px 0;
    overflow: hidden;
  }

  .slick-prev,
  .slick-next {
    position: absolute;
    top: -12px;
    right: 0;
    width: 56px;
    height: 56px;
    border: 1px solid #D5D5D5;
    background: none;
    display: flex;
    align-items: center;
    justify-content: center;
    &:before {
      display: none;
    }
  }

  .slick-prev {
    left: calc(100% - 125px);
  }

}
