.steps {
  padding: 100px 0;
  &__inner {
    margin-top: 48px;
    display: flex;
    align-items: stretch;
    justify-content: space-between;
  }

  &__item {
    position: relative;
    padding: 30px;
    width: 353px;
    min-height: 394px;
    background-color: #043C77;
    &-content {
      margin-top: 30px;
    }
    &-icon {
      width: 64px;
      height: 64px;
      fill: #fff;
    }
    &-title {
      font-weight: 500;
      color: #fff;
      margin-bottom: 24px;
    }
    &-descr {
      font-weight: 400;
      font-size: 16px;
      line-height: 140%;
      color: #FFFFFF;
      &--mini {
        margin-top: 15px;
        font-size: 12px;
      }
    }
    &-time {
      position: absolute;
      top: 30px;
      right: 30px;
      font-weight: 600;
      font-size: 15px;
      color: #FFFFFF;
      img {
        margin-top: 17px;
      }
    }
    &-arrow {
      width: 59px;
      height: 14px;
      fill: var(--main-color);
    }
  }
}
