.partners {
  padding: 50px 0 100px 0;
  position: relative;
  &__slider {
    border-bottom: 1px solid #E7E7E7;
  }

  .slick-track {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }

  .slick-list {
    padding: 52px 0 50px 0;
    overflow: hidden;
  }

  .slick-dots {
    position: absolute;
    bottom: -52px;
    left: 50%;
    transform: translateX(-50%);
    list-style-type: none;
    display: flex;
    align-items: center;
    justify-content: center;
    list-style-type: none;
    li {
      margin-right: 24px;
      width: 4px;
      height: 4px;
      border-radius: 50%;
      background: #d4d4d4;
      button {
        width: 4px;
        height: 4px;
        &:before {
          width: 8px;
          height: 8px;
          opacity: 0;
        }
      }
    }

    .slick-active {
      width: 8px;
      height: 8px;
      background: var(--main-color);
      border-radius: 50%;
    }
  }
}
