/* stylelint-disable */

@mixin for-desktop {
  @media (max-width: (1120px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (960px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (576px)) {
    @content;
  }
}

@mixin other-adaptive {
  @media (max-width: (420px)) {
    @content;
  }
}
